import { h, Component } from 'preact';

import style from './style';

export default class About extends Component {
	render() {
		return (
			<article>
				<p>	Sukurkime piein, tapsiant ms istorijos dalimi ir gatvs meno kriniu!</p>
				<p>
				Usiregistruokite, gaukite pikseli ir pradkite pieti. Nuo registracijos pradios kas valand gausite po vien piksel. Dienos pikselius galite kaupti (iki 24 per dien), bet j neinaudojus iki tos dienos pabaigos, vidurnakt pikseliai bus anuliuojami. Iekokite bendramini pasirinkto pieinio aminimui ir koordinuokite veiksmus.
				</p>
				<p>Ateikite balsuoti  Europos Parlamento rinkimus, skenuokite balsavimo apylinkse esanius QR kodus ant Vote4Art plakat ir gaukite dar daugiau pikseli. Taip prisidsime prie Europos ateities krimo ir bendrai sukursime virtual piein, kuris taps ms vis istorijos dalimi.
				</p>
			</article>

		);
	}
}



